




























































export default {
	data() {
		return {
			noneimg: '../../../static/img/nopicture.png',
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			headpicUrl: '',
			nickName: '',
			activeStatusInfo: '',
			my_data: {
				activeStatus: 0,
				endDate: 0,
				pledgeSocre: 0,
				seriesDays: 0,
				startDate: 0,
				supportTotalNum: 0,
				targetDays: 7,
				targetPuffsTime: 0,
				targetScore: 0
			},
			list_msg: []
		};
	},
	onLoad: function() {
		this.headpicUrl = this.$store.state.headpicUrl;
		this.nickName = this.$store.state.nickName;
	},
	onShow: function() {
		this.initData();
	},
	methods: {
		initData() {
			let that = this;
			this.$api.activePage({
				userCode: this.$store.state.userCode,
				token: this.$store.state.token
			}, function(res) {
				console.log(res);
				if (res.data.status == 1) {
					that.setChallengeData(res.data.data);
				}
			}, function(err) {
				console.log(err)
			});
		},
		setChallengeData(value) {
			if (typeof value === 'object') {
				this.my_data = value;
				// 活动状态（ 0-失败 1-进行中 2-成功 3-已领取奖励）
				switch (this.my_data.activeStatus) {
					case 0:
						this.activeStatusInfo = '挑战失败-点击重新挑战';
						break;
					case 1:
						this.activeStatusInfo = '挑战进行中';
						break;
					case 2:
						this.activeStatusInfo = '挑战成功-点击领取奖励';
						break;
					case 3:
						this.activeStatusInfo = '已领取奖励';
						break;
					default:
						break;
				}
			}
			if (Array.isArray(value.puffsList)) {
				this.list_msg = value.puffsList;
			}
		},
		upper: function(e) {
			console.log(e)
		},
		lower: function(e) {
			console.log(e)
		},
		scroll: function(e) {
			console.log(e)
			this.old.scrollTop = e.detail.scrollTop
		},
		goTop: function(e) {
			// 解决view层不同步的问题
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			})
		},
		tryAgain: function() {
			let that = this;
			if (this.my_data.activeStatus == 2) {
				this.$api.getActiveReward({
					userCode: this.$store.state.userCode,
					token: this.$store.state.token,
				}, function(res) {
					if (res.data.status == 1) {
						uni.showToast({
							title: '领取成功获得积分' + res.data.data,
							icon: 'success',
							mask: false,
							duration: 1500
						});
						that.my_data.activeStatus = 3;
					} else {
						uni.showToast({
							title: res.data.msg,
							mask: false,
							duration: 1500
						});
					}
				}, function(err) {
					console.log(err)
				});
			} else if (this.my_data.activeStatus == 0) {
				uni.navigateTo({
					url: '../challengeTarget/challengeTarget'
				});
			}
		}
	},
}



